import { Component,ViewChild } from '@angular/core';
import { Platform, Nav, AlertController } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { SettingProvider } from '../providers/setting/setting';
import { TranslateService } from '@ngx-translate/core';
import { UserService } from '../shared/index';


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;

  rootPage
  appMenuItems: Array<any>;

  

  //language setting
  RadioOpen: boolean;
  RadioResult;
  langs: [{}];

  constructor(platform: Platform, statusBar: StatusBar, splashScreen: SplashScreen, private translate: TranslateService, public alerCtrl: AlertController, public userService: UserService
  ) {
    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();

      translate.addLangs(["en", "zh", "tw"]);
      translate.setDefaultLang('zh');
      platform.registerBackButtonAction(function (event) {
        event.preventDefault();
    }, 100);

    });

    this.setRootPage()
    this.appMenuItems = [ 
      {title: SettingProvider.pageSettings.TabsPage.pageTitle, componentName: SettingProvider.pageSettings.TabsPage.componentName, icon: SettingProvider.pageSettings.TabsPage.pageIcon},
    ];
  }



  setRootPage(){
    // this.storageService.retrieveValueByKey(SettingProvider.appStorageKeys.ISAUTH).then(
    //   user => {
    //     this.rootPage = SettingProvider.pageSettings.TabsPage.componentName;
    //   }, 
    //   error =>{
    //     this.rootPage = SettingProvider.pageSettings.TutorialPage.componentName;
    //   }
    // )
    this.rootPage = SettingProvider.pageSettings.TutorialPage.componentName;
  }

  openPage(page) {
    this.nav.setRoot(page.componentName);
  }

  changeLanguage() {
    this.langs = [{ language: "English", type: "en" }, { language: "", type: "zh" }, { language: "", type: "tw" }]

    let alert = this.alerCtrl.create();
    alert.setTitle('');
    for (let lang of this.langs) {
      alert.addInput({
        type: 'radio',
        label: lang["language"],
        value: lang["type"],
        checked: (lang["type"] == this.translate.getDefaultLang() ? true : false)
      });
    }
    alert.addButton('');
    alert.addButton({
      text: '',
      handler: data => {
        this.RadioOpen = false;
        this.RadioResult = data;
        this.translate.setDefaultLang(data);
        this.translate.use(data);
      }
    });

    alert.present().then(() => {
      this.RadioOpen = true;
    });
  }

  logout(){
    this.userService.purgeAuth()
    //this.nav.setRoot(SettingProvider.pageSettings.LoginPage.componentName)
    this.nav.insert(0,SettingProvider.pageSettings.LoginPage.componentName);
    this.nav.popToRoot();
  }
}



