import {Injectable, Inject, forwardRef} from "@angular/core";
import { ApiService } from "../api.service";
import {Observable} from "rxjs/Rx";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import {ReplaySubject} from "rxjs/ReplaySubject";
import { SettingProvider } from "../../../providers/setting/setting";
import { Class, userClassSchedule, CommonService, ClassDetail, Homework } from "../../index";
import { RegProgram } from "../../models/regProgram.model";

@Injectable()
export class ClassService{

    path1: string;
    path2: string;
    path3: string;

    constructor(private apiService: ApiService, @Inject(forwardRef(() => CommonService)) private commonService ){
        this.path1 = '/classSchedule';
        this.path2 = '/classHomework';
        this.path3 = '/regPrograms'
    }

    getClassSchedule(userClass: userClassSchedule) :Observable<Class[]> {
        return this.apiService.get(this.path1, userClass)
        .map(
        data =>{
            console.log(data)
           let d = data.map(x =>
             this.convertToClass(x)
            );
           console.log(d)
           return d;
         }
    );
    }

    getClassDetailByRegProgram(regProgram: RegProgram) :Observable<ClassDetail[]> {
        return this.apiService.get(this.path2, regProgram)
        .map(
        data =>{
            console.log(data)
           let d = data.map(x =>
             this.convertToClassDetail(x)
            );
           console.log(d)
           return d;
         }
    );
    }

    getRegPrograms(userClass: userClassSchedule) :Observable<RegProgram[]> {
        return this.apiService.get(this.path3, userClass)
        .map(
        data =>{
            console.log(data)
           let d = data.map(x =>
             this.convertToRegProgram(x)
            );
           console.log(d)
           return d;
         }
    );
    }

    convertToClass(Data){
        let c = new Class();
        c.id = Data.ClassID;
        c.studentId = Data.StudentID;
        c.campusId = Data.CampusID;
        c.teacherId = Data.InstructorID;
        c.regId = Data.RegpID;
        c.programId = Data.ProgramID;
        c.date = this.commonService.getDateFromFullDate(Data.ClassStart);
        c.startTime = this.commonService.getTimeFromFullDate(Data.ClassStart);
        c.endTime = this.commonService.getTimeFromFullDate(Data.ClassEnd); 
        c.status = Data.Status
        c.teacherFirstName = Data.FirstName;
        c.teacherLastName = Data.LastName;
        c.campus = Data.CampusName;
        c.programName = Data.ProgramName;
        return c;
    }

    convertToClassDetail(Data){
        let c = new ClassDetail();
        c.id = Data.ClassID;
        c.campusId = Data.CampusID;
        c.teacherId = Data.InstructorID;
        c.regId = Data.RegpID;
        c.programId = Data.ProgramID;
        c.date = this.commonService.getDateFromFullDate(Data.ClassStart);
        c.startTime = this.commonService.getTimeFromFullDate(Data.ClassStart);
        c.endTime = this.commonService.getTimeFromFullDate(Data.ClassEnd); 
        c.status = Data.Status
        c.teacherFirstName = Data.FirstName;
        c.teacherLastName = Data.LastName;
        c.campus = Data.CampusName;
        c.programName = Data.ProgramName;
        c.homeworkDone = Data.Homework;
        c.homeWorkDetail = Data.HomeworkDetail
        return c;
    }

    convertToRegProgram(Data){
        let rg = new RegProgram();
        rg.id = Data.RegpID;
        rg.studentId = Data.StudentID;
        rg.programId = Data.ProgramID;
        rg.startDate = this.commonService.getDateFromFullDate(Data.StartDate);
        rg.endDate = this.commonService.getDateFromFullDate(Data.EndDate);
        rg.programName = Data.ProgramName;
        rg.homework = [];
        return rg;
    }
}


