import {Injectable} from "@angular/core";
import { LoadingController,Loading, AlertController,ToastController   } from 'ionic-angular';
import * as moment from 'moment/moment';

@Injectable()
export class CommonService{

    //private loader : Loading;
    private utcOffSet = '+0000'
    private dateFormt = 'YYYY-MM-DD';
    private timeFormat = 'HH:mm a';

    constructor(private loadingCtrl: LoadingController, private alertCtrl: AlertController, private toastCtrl: ToastController){
       
    }


    public presentLoading(){
        let loader = this.loadingCtrl.create({
           // content: "Loading data...",
            spinner: 'dots',
            dismissOnPageChange: true
        });
       loader.present();
       return loader;
    }

    //dismissLoading should only be used when error show page, if new page will show , no need to call
    public dismissLoading(loader : Loading){
      try{
        loader.dismiss().catch(function(err){
          
        });
      }catch(e){

      }
        
    }
  

    public presentAlert(title: string, content: string) {
        let alert = this.alertCtrl.create({
          title: title,
          subTitle: content,
          buttons: ['Dismiss']
        });
        alert.present();
      }

      public presentNetWorkRequestError(){
        const title = 'NetWork Error'
        const subTitle = 'Please check your network connection'
        this.presentAlert(title,subTitle);
      }

      public presentToast (message : string){
        let toast = this.toastCtrl.create({
          message: message,
          duration: 2000
        });
        toast.present();
      }

      public getDateFromFullDate(fullDate: string){
        let date = moment(fullDate).utcOffset(this.utcOffSet).format(this.dateFormt)
        return date;
              
      }

      public getTimeFromFullDate(fullDate: string){
        let time = moment(fullDate).utcOffset(this.utcOffSet).format(this.timeFormat)
        return time;
      }

      // 'YYYY-MM-DD';
      public getSplitDate(date){
      let  parts = date.split("-");
     ;
          let ds= {
            year:  parseInt( parts[0], 10),
            month:   parseInt( parts[1], 10) -1,
            date:  parseInt( parts[2], 10),
          }
          return ds;
      }

}


