import {Injectable, Inject, forwardRef} from "@angular/core";
import { ApiService } from "../api.service";
import {Observable} from "rxjs/Rx";
import { BehaviorSubject } from "rxjs/BehaviorSubject";
import {ReplaySubject} from "rxjs/ReplaySubject";
import { User, UserDetail, StorageService } from "../../index";
import { SettingProvider } from "../../../providers/setting/setting";

@Injectable()
export class UserService{

    private path1:string;
    private path2:string;
    private currentUserSubject = new BehaviorSubject<User>(new User());
    public currentUser = this.currentUserSubject.asObservable().distinctUntilChanged();

    private isAuthenticatedSubject = new ReplaySubject<boolean>(1);
    public isAuthenticated = this.isAuthenticatedSubject.asObservable();

    constructor(private apiService: ApiService, @Inject(forwardRef(() => StorageService)) private storageService){
        this.path1 = '/users';
        this.path2 = '/userDetails'
    }

    setAuth(user:User) {
          // Set current user data into observable
          
          this.currentUserSubject.next(user);
          // Set isAuthenticated to true
          this.isAuthenticatedSubject.next(true);
          this.storageService.storeValueByKey(SettingProvider.appStorageKeys.ISAUTH, user);
    }

    purgeAuth() {
        // Set current user to an empty object
        this.currentUserSubject.next(new User());
        // Set auth status to false
        this.isAuthenticatedSubject.next(false);
        this.storageService.removeKey(SettingProvider.appStorageKeys.ISAUTH);
    }

    authenticate(user: User) :Observable<User> {
        return this.apiService.get(this.path1, user)
        .map(
        data =>{
            if(data.length == 1){
                //add some logic convert respond to user
                let u  = this.covertToUser(data[0])
                this.setAuth(u);
                return u;
            }else{
                this.purgeAuth();
            }
         }
    );
    }

      // Update the user on the server
      update(user):Observable<User> {
        return this.apiService
            .put(this.path1, {user})
            .map(data => {
                // Update the currentUser observable
                this.currentUserSubject.next(data.user);
                return data.user;
            });
    }

    getUserDetail(user: User) :Observable<UserDetail> {
        // let userDetail: UserDetail = new UserDetail();
        // userDetail.id = user.id;
        return this.apiService.get(this.path2, user)
        .map(
        data =>{
            if(data.length == 1){
                let ud  = this.covertToUserDetail(data[0])
                return ud;
            }
         }
    );
    }

    covertToUser(data){
        let u = new User();
        u.id = data.StudentID;
        u.username = data.Username;
        u.password = data.Password;
        return u;
    }

    covertToUserDetail(data){
        let ud = new UserDetail();
        ud.id = data.StudentID;
        ud.firstName = data.FirstName;
        ud.lastName = data.LastName;
        ud.cellPhone = data.CellPhone;
        ud.homePhone = data.HomePhone;
        ud.contactName = data.ContactName;
        ud.gender = data.Gender;
        ud.schoolName = data.SchoolName;
        ud.schoolGrade = data.SchoolGrade;
        ud.email = data.Email;
        ud.pic= data.Picture;
        ud.regDate = data.RegDate;
        return ud;
    }

}


